import React from "react";

const Difficulty = ({onClick1, onClick2, dif}) => {
    return (
        <div className='dif'>
            Difficulty {dif}
            <div className="addlvl" onClick={() => onClick1()}>+</div>
            <div className="addlvl" onClick={() => onClick2()}>-</div>
        </div>)
}

export default Difficulty