import React from "react";
import Restartbtn from "./Restartbtn";
import Score from "./Score";
import Difficulty from "./Difficulty";
import Card from "./Card";


const COLUMN = 4 // number of columns by default


//import all images from static/cards folder

function importAll(r) {
    return r.keys().map(r);
}

const images = importAll(require.context('./static/cards', false, /\.(png|jpe?g|svg|gif)$/));
const imageslist = images.map((image) => image["default"])


class App extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            nbCards: COLUMN,                // nb of cards per column
            cards: this.CreateCards(),      // cards in the game
            returnedcards: [],              // flipped cards
            guessed: [],                    // guessed pairs of cards
            score: 0,                       // score
        }
    }

    Randomizearray(array) {
        return array.sort(() => .5 - Math.random());
    }

    CreateCards = (index = COLUMN) => {
        const array = this.Randomizearray(imageslist)
        let count = 0
        const result = []
        const size = index * 3
        while (result.length < size) {
            result.push(array[count], array[count])
            count++
        }
        return this.Randomizearray(result)
    }

    RestartCards = () => {
        const tempcards = this.CreateCards(this.state.nbCards);
        this.setState((prevState) => {
                return {
                    cards: tempcards,
                    returnedcards: [],
                    guessed: [],
                    score: 0
                }
            }
        );
    }
    addCards = () => {
        if (this.state.nbCards < 12) {
            this.setState((prevState) => {
                return {nbCards: prevState.nbCards + 2}
            })
        }
    }
    rmCards = () => {
        if (this.state.nbCards > 2) {
            this.setState((prevState) => {
                return {nbCards: prevState.nbCards - 2}
            })
        }
    }


    HandleCardClick = (index) => {
        const {guessed, returnedcards} = this.state
        if (!guessed.includes(index)) {
            if (returnedcards.length === 0) {
                this.setState({returnedcards: [index]})
            } else if (returnedcards.length === 1 && returnedcards[0] !== index) {
                this.CheckPair(index)
            }
        }
    }

    CheckReturn = (index) => {

        const {returnedcards, guessed} = this.state
        if (returnedcards.includes(index) || guessed.includes(index)) {
            if (this.CheckGuessed(index))
                return 'visible guessed'
            if (returnedcards.length === 2)
                return 'visible wrong'
            return 'visible'
        }
        return 'hidden'
    }

    CheckPair = (index) => {
        const {cards, returnedcards} = this.state
        if (cards[returnedcards[0]] === cards[index]) {
            this.setState((prevState) => {
                const returned = prevState.returnedcards[0];
                const guessedcards = [index, returned];

                return {
                    guessed: [...prevState.guessed, ...guessedcards],
                    score: prevState.score += 3,
                    returnedcards: [returnedcards[0], index]
                }
            })
        } else {
            this.setState((prevState) => {
                return ({score: prevState.score -= 1, returnedcards: [returnedcards[0], index]})
            })
        }

        setTimeout(() => this.setState({returnedcards: []}), 1000)
    }
    CheckGuessed = (index) => {
        const {guessed} = this.state;
        if (guessed.includes(index)) {
            return true
        }
        return false
    }
    CheckVictory() {
        return this.state.guessed.length === this.state.cards.length
    }


    render() {
        return (
            <div>
                <div className="navbar">
                    <Restartbtn onClick={this.RestartCards}/>
                    <Score score={this.state.score} victory={this.CheckVictory()}/>
                    <Difficulty onClick1={this.addCards} onClick2={this.rmCards} dif={this.state.nbCards / 2}/>
                </div>
                <div className="memory">
                    <div className="cards-container">
                        {this.state.cards.map((card, index) =>
                            <Card
                                index={index}
                                card={card}
                                returned={this.CheckReturn(index)}
                                onClick={this.HandleCardClick}
                            />
                        )}
                    </div>

                </div>
            </div>
        );
    }
}


export default App