import back from "./static/Backimg.png";
import React from "react";

const Card = ({card, index, onClick, returned}) => (
    <div className='card-container' onClick={() => onClick(index)}>
        <div className={`card ${returned}`}>
            <img alt='' src={back} className="side back"/>
            <img alt='' className="side front" src={card}/>

        </div>
    </div>
)

export default Card